.max-home .mobile-model3{
	width: 100%;
	height: 100%;
}
.max-home .mobile-model3-video{
	padding-top: 4.28rem;
	width: 100%;
	height: 100%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.max-home .model3-video1-info{
	/*overflow: hidden;*/
	position: absolute;
	top: 50px;
	/*left: 155px;*/
	height: 134px;
	line-height: 100px;
	z-index: 11;	
}
.max-home .model3-video1-info>.icon{
		float: left;
		width: 100px;
		height: 134px;
		background-color: #000;
}
.max-home .model3-video1-info>div>img{	
	display: block;
	margin: 31px 16.5px;
	height: 71px;
	width: 67px;
}
.max-home .model3-video1-info>p{
	float: left;
	margin-top: 41px;
	margin-left: 5px;
	width: 323px;
	line-height: 24px;
	font-size: 20px;
	color: #fff;
}
.max-home .model3-video2-info{
	/*overflow: hidden;*/
	/*right: 155px;*/
}
.max-home .model3-video2-info>div{
	float: right;
}
.max-home .model3-video2-info>p{
	float: right;
	text-align: right;
}
.max-home .model4-video1-info>div>img{
	margin-top: 54px;
	width: 63px;
	height: 26px;
}
.max-home .model4-video2-info{
	/*right: 142px;*/
}
.max-home .model4-video2-info>div>img{
	margin: 46px auto;
	width: 49px;
	height: 42px;
}
.max-home .model5-video2-info{
	right: 142px;
}
.max-home .model5-video2-info>div>img{
	margin: 44px auto;
	width: 45px;
	height: 45px;
}
.max-home .model5-video1-info>div>img{
	margin: 44px auto;
	width: 45px;
	height: 45px;
}

.mobile-model3-video-title{
	position: relative;
	z-index: 1;
	margin-top: 1.857rem;
	line-height: 2.07rem;
	font-size: 1.71rem;
	text-align: center;
	color: #fff;
}
.mobile-model3-video-info{
	position: absolute;
	left: 50%;
	margin-left: -9.3rem;
	z-index: 1;
}
.mobile-model3-video-info>div{
	float: left;
}
.mobile-model3-video-info>div>img{
	width: 3rem;
	height: 3rem;
}
.mobile-model3-video-info>p{
	margin-top: 0.3rem;
	margin-left: 0.5rem;
	float: left;
	line-height: 1.4rem;
	font-size: 1rem;
	color: #fff;
	width: 13.35rem;
}
.mobile-model3-video-info .mobile-model3-video-details{
	line-height: 2.8rem;
}
.mobile-model3-video>video{
	position: absolute;
	top: 0;
	left: 50%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
	z-index: -1;
}

#mobile-model3-canvas,#mobile-model31-canvas,#mobile-model4-canvas,#mobile-model41-canvas,#mobile-model5-canvas,
#mobile-model51-canvas,#mobile-model52-canvas,#mobile-model53-canvas{
	position: absolute;
	top: 0;
	left: 0;
	z-index: 11;
}

.canvas-line{
	border-width: 0 0 1px;
    height: 114px;
    min-width: 100%;
    position: absolute;
    top: 0px;
    z-index: 11111;
    border-style: solid;
    border-color: #777;
}
.canvas-line>div{
	width: 80px;
	height: 1px;
	background-color: #777;
	position: absolute;
	bottom: 0;
	-webkit-transform: rotate(45deg) translate3d(68px,-27.5px,0px);
	        transform: rotate(45deg) translate3d(68px,-27.5px,0px);
}
.canvas-line-right>div{
	-webkit-transform: rotate(-45deg) translate3d(-68px,-27.5px,0px);
	        transform: rotate(-45deg) translate3d(-68px,-27.5px,0px);
}
.model3-video1-info{
	display: none;
}



