.mobile-model5-pic{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.mobile-model5-pic>img,.mobile-model5-pic>video{
	position: absolute;
	height: 100%;
	top: 0;
	left: 50%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
	padding-top: 4.28rem;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	z-index: 0;	
}




