.max-home .mobile-service{
	position: relative;
	width: 100%;
	height: 100%;
}
.max-home .mobile-service1-slide{
	padding-top: 6.35rem;
}
.max-home .mobile-service2-slide{
	padding-top: 4.5rem;
}
.max-home .mobile-service1-slide-inner{
	position: relative;
	width: 100%;
	height: 29.64rem;
}
.max-home .mobile-service2-slide-inner{
	position: relative;
	width: 100%;
	height: 18.28rem;
}
.max-home .service1-slide-list{
	position: absolute;
	top: 0;
	left: 0;
	list-style: none;
	width: 100%;
	height: 100%;
}
.max-home .service2-slide-list{
	left: 50%;
	margin-left: -9.71rem;
	width: 19.42rem;
}
.max-home .service1-slide-list>li{
	position: absolute;
	top: 0;
	left: 0;
	display: none;
	width: 100%;
	height: 100%;
}
.max-home .service1-slide-list>li>img{
	display: block;
	width: 100%;
	height: 100%;
}
.max-home .service1-slide-controls{
	position: absolute;
	width: 100%;
	height: 100%;
}
.max-home .service1-slide-controls>a{
	position: absolute;
	top: 50%;
	margin-top: -1.07rem;
}
.max-home .service1-slide-controls>a:first-child{
	left: 1.14rem;
}
.max-home .service1-slide-controls>a:last-child{
	right: 1.14rem;
}
.max-home .mobile-service-icon{
	margin: 2.85rem auto 0;
	width: 15.22rem;
	height: 5rem;
}
.max-home .mobile-service-icon>img{
	width: 100%;
	height: 100%;
}
.max-home .mobile-service2-info{
	margin: 1.285rem auto 1.57rem;
	width: 24.92rem;
	line-height: 1.357rem;
}
.max-home .mobile-service2-info>p{
	line-height: 1.35rem;
	font-size: 1.1rem;
	color: #9B9B9B;
	text-align: center;
}
.max-home .mobile-service2-click{
	text-align: center;
	margin: 0 auto;
	width: 22.5rem;
	height: 3.21rem;
	line-height: 3.21rem;
	background-color: #FF8800;
}
.max-home .mobile-service2-click>img{
	display: inline-block;
	width: 1.28rem;
	height: 1.42rem;
	vertical-align: middle;
}
.max-home .mobile-service2-click>p{
	margin-left: 0.3rem;
	display: inline-block;
	color: #000000;
	font-size: 1rem;
}



