.max-home .mobile-model2-info{
	text-align: center;
	width: 100%;
	padding: 8.42rem 0 1.64rem;
}
.max-home .mobile-model2-info>div{
	position: relative;
	display: inline-block;
	
}
.max-home .mobile-model2-icon{
	display: inline-block;
	margin-right: 27px;
	display: inline-block;
	width: 53px;
	height: 53px;
	border: 1px solid #fff;
}
.max-home .mobile-model2-icon>i{
	display: inline-block;
	width: 53px;
	height: 53px;
	position: absolute;
	top: 9px;
	left:-9px;
	border: 1px solid #fff;
}
.max-home .mobile-model2-icon>span{
	position: absolute;
	top: 20px;
	left: 12px;
	color: #fff;
	font-size: 20px;
	font-weight: 300;
}
.max-home .mobile-model2-pic{
	position: relative;
	width: 100%;
	height: 18.35rem;
}
.max-home .mobile-model2-pic>img{
	width: 25rem;
    height: 15rem;
    position: absolute;
    top: 1.28rem;
    left: 1.1rem;
    -webkit-transform: rotateX(35deg);
            transform: rotateX(35deg);
}
.max-home .mobile-model2-des{
	text-align: left;
	display: inline-block;
	line-height: 1.36rem;
}
.max-home .mobile-model2-des>p{
	color: #9B9B9B;
	font-size: 1.14rem;
	line-height: 1.35rem;
}
.max-home .mobile-model2-des>h3{
	font-size: 1.14rem;
	color: #fff;
}
.max-home .mobile-model2-model2{
	margin-top: 5.57rem;
}
.max-home .mobile-model2-model2>img{
	display: block;
	margin: 0 auto;
	width: 3.71rem;
	height: 2.57rem;
}
.max-home .size{
	margin: 2.71rem 0  1.42rem;
	width: 100%;
	text-align: center;
	list-style: none;
}
.max-home .size>li{
	margin-right: 1.14rem;
	display: inline-block;
	font-size: 0.85rem;
	font-weight: 300;
	color: #fff;
	line-height: 1.35rem;
}
.max-home .size>li:last-child{
	margin: 0;
}
.max-home .size>li>span{
	margin-left: 0.5rem;
	font-size:1.14rem;
}
/*PC 端canvas*/
.max-home #pc-model2-canvas,.max-home #pc-model3-canvas,.max-home #pc-model4-canvas,.max-home #pc-model5-canvas{
	display: block;
	position: absolute;
	top: 0;
	left: 0;
}
.max-home #mobile-model2-canvas{
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 11;
}

.pcModelTwo-size{
	display: none;
	position: absolute;
	color: #fff;
}
.pcModelTwo-size>span:first-child{
	font-size: 12px;
}
.pcModelTwo-size>span:last-child{
	margin-left: 3px;
	font-size: 14px;
}


