.max-home .mobile-shopping{

}
.max-home .mobile-shopping-item{
	margin-bottom: 3.21rem;
}
.max-home .mobile-shopping-item-pic{
	width: 100%;
	height: 14.78rem;
}
.max-home .mobile-shopping-item-pic>img{
	width: 100%;
	height: 100%;
}
.max-home .mobile-shopping-item-details{
	padding:0 2.14rem;
}
.max-home .mobile-shopping-item-title{
	margin: 1.42rem auto 1.85rem;
	color: #fff;
}
.max-home .mobile-shopping-item-title>h1{
	width: 19.64rem;
	height: 5.14rem;
	font-size: 2.14rem;
	line-height: 2.57rem;	
}
.max-home .mobile-shopping-item-title>h4{
	margin-top: 0.57rem;
	width: 18.21rem;
	font-size: 1.14rem;
	line-height: 1.35rem;
}
.max-home .mobile-shopping-kit>h2{
	margin: 0;
	width: 13.92rem;
	height: 2.07rem;
	line-height: 2.07rem;
	color: #F8E71C;
	font-size: 1.71rem;
}
.max-home .mobile-shopping-kit>h5{
	margin-top: 0.57rem ;
	width: 12.5rem;
	height: 4.07rem;
	line-height: 1.35rem;
	color: #F8E71C;
}
.max-home .mobile-shopping-kit>p{
	margin-top: 0.57rem ;
	margin-bottom: 0.64rem ;
	width: 14.71rem;
	height: 1.35rem;
	color: #F8E71C;
	font-size: 1.14rem;
	text-decoration: line-through;
}
.max-home .mobile-shopping-kit>button{
	width: 22.5rem;
	height: 3.21rem;
	line-height: 3.21rem;
	text-align: center;
	color: #000;
	border: none;
	background-color: #F8E71C;
}
.max-home .mobile-shopping-stk{
	margin-top: 1.71rem;
}
.max-home .mobile-shopping-stk-icon{
	width: 8.71rem;
	height: 2.85rem;
}
.max-home .mobile-shopping-stk-icon>img{
	width: 100%;
	height: 100%;
}
.max-home .mobile-shopping-stk>h5{
	color: #FF8800;
}
.max-home .mobile-shopping-stk>p{
	color: #FF8800;
}
.max-home .mobile-shopping-stk>button{
	background-color: #FF8800;
}
.max-home .mobile-stk-info{
	margin-left: 2.14rem;
	margin-bottom: 2.5rem;
	width: 21.57rem;
	height: 16.21rem;
}
.max-home .mobile-stk-info>p{
	line-height: 1.35rem;
	font-size: 1.14rem;
	color: #fff;
}
.max-home .mobile-stk-info>p:nth-child(3){
	margin-bottom: 2rem;
}
.max-home .mobile-stk-info>p>a{
	
}
.max-home .mobile-stk-info>p>span{
	color: #F8E71C;
}
.max-home .mobile-Brand-links{
	margin-left: 2.14rem;
}
.max-home .mobile-head{
	overflow: hidden;
}
.max-home .mobile-head>img{
	float: left;
	margin-right: 1.5rem;
	display: block;
	width: 1.57rem;
	height: 1.357rem;
}
.max-home .mobile-Brand{
	overflow: hidden;
	margin-top: 1.64rem;
	width: 14.86rem;
}
.max-home .mobile-Brand>img{
	margin-right: 0.57rem;
	margin-bottom: 0.64rem;
	display: block;
	float: left;
	width: 3.14rem;
	height: 1.71rem;
}
.max-home .mobile-copyright{
	margin:2.57rem 0 2.35rem 2.14rem;
	width: 15rem;
}
.max-home .mobile-copyright>p{
	line-height: 1.07rem;
	font-size: 0.857rem;
	color: #fff;
}



