.max-home .mobile{
	position: relative;
	width: 100%;
	height: 100%;
}
.max-home .home{
	position: relative;
	width: 100%;
	height: 100%;
}
/*video*/
.max-home .mobile-home-video{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.max-home .mobile-home-video>video{
	position: absolute;
	top: 0;
	left: 50%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
}
/*home-content*/
.max-home .mobile .home-content{
	position: absolute;
	z-index: 0;
	padding: 2.14rem 0;
	width: 100%;
	margin-top: 4.28rem;
	margin-left: 2.14rem;
}
.max-home .mobile .home-content>h1{
	margin-left: 0.71rem;
	width: 9.78rem;
	height: 1.71rem;
}
.max-home .home-content img{
	display: block;
	width: 100%;
	height: 100%;
}
.max-home .mobile .home-content-icon{
	margin-top: 0.214rem;
	margin-left: 0.36rem;
}
.max-home .mobile .home-content-icon>img:first-child{
	width: 10.64rem;
	height: 5rem;
}
.max-home .mobile .home-content-icon>img:last-child{
	margin-top: 0.64rem;
	width: 10.14rem;
	height: 2.71rem;
}
.max-home .mobile .home-content>p{
	margin-left: 0.36rem;
	width: 20.07rem;
	height: 7.74rem;
	line-height: 2.58rem;
	font-size: 2.14rem;
	color: #FFFFFF;
}
.max-home .mobile .home-content-button{
	margin-top: 2.93rem;
	
}
.max-home .mobile .home-content-button>button{
	display: block;
	width: 11.42rem;
	height: 3.21rem;
	color: #000;
	border: none;
}
.max-home .mobile .home-content-button>button:first-child{
	margin-bottom: 0.86rem;
	background-color: #F8E71C;
}
.max-home .mobile .home-content-button>button:last-child{
	background-color: #FFFFFF;
}

/*home-bottom*/
.max-home .home-bottom{
	width: 100%;
	position: absolute;
	bottom: 0.93rem;
	left: 0;
}
.max-home .home-bottom>h3{
	font-size: 1.07rem;
	color: #F8E71C;
	text-align: center;
}
.max-home .home-bottom-arrow{
	margin-top: 0.5rem;
	height: 1.71rem;
	margin: 0 auto;
    text-align: center;
	position: relative;
	width: 100%;
}
.max-home .home-bottom-arrow>span{
	position: absolute;
	top: 0;
	left: 50%;
	margin-left: -1.5px;
	display: inline-block;
	width: 3px;
	height: 1rem;
	background-color: #F8E71C;
}
.max-home .home-bottom-arrow>i{
	position: absolute;
	top: 1rem;
	left: 50%;
	margin-left: -5px;
	border-color: #F8E71C;
    border-style: solid;
    border-width: 10px 5px 0;
    border-left-color: transparent;
    border-right-color: transparent;	
}





