.max-home .mobile-video{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.max-home .mobile-video>video{
	position: absolute;
    width: auto;
    height: 100%;
    top: 0;
    left: 50%;
    -webkit-transform: translate3d(-50%,0,0);
            transform: translate3d(-50%,0,0);
}
.max-home .mobile-video .video-hide{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #000;
	display: none;
}
.max-home .mobile-video .video-hide>.x{
	position: absolute;
	top: 2rem;
	right: 1.42rem;
	width: 1.42rem;
	height: 1.42rem;
}
.max-home .mobile-video .video-hide>.x>span{
	display: block;
	height: 2px;
	width: 1.6rem;
	background-color: #fff;
	border-radius: 1px;
	margin-bottom: -2px;
}
.max-home .mobile-video .video-hide>.x>span:first-child{
	-webkit-transform: rotate(45deg);
	    -ms-transform: rotate(45deg);
	        transform: rotate(45deg);
}
.max-home .mobile-video .video-hide>.x>span:last-child{
	-webkit-transform: rotate(-45deg);
	    -ms-transform: rotate(-45deg);
	        transform: rotate(-45deg);
}
.max-home .mobile-video .video-hide>video{
	position: absolute;
	top: 50%;
	left: 0;
	-webkit-transform: translateY(-50%);
	    -ms-transform: translateY(-50%);
	        transform: translateY(-50%);
	width:100%;
	height:auto;
	z-index:1;
}



