.max-home .mobile-box-countdown{
	padding-top: 4.28rem;
	
	
}
.max-home .mobile-box-countdown>div{
	height: 4.28rem;	
	background-color:#1A1A1A ;
}
.max-home .mobile-box-countdown h2{
	text-align: center;
	line-height: 1.43rem;
	font-size: 0.93rem;
	color: #F8E71C;
}
.max-home .mobile-time-list{
	font-size: 0;
	text-align: center;
	list-style: none;
}
.max-home .mobile-time-list>li{
	display: inline-block;
	line-height: 1.64rem;
	color: #fff;
}
.max-home .mobile-time-list>li>span:first-child{
	font-size: 1.36rem;
}
.max-home .mobile-time-list>li>span:last-child{
	margin: 0 0.3rem;
	font-size: 0.8rem;
}
.max-home .mobile-box-video{
	width: 100%;
	height: 18.357rem;
}
.max-home .mobile-box-video>img{
	width: 100%;
	height: 100%;
}
.max-home .mobile-box-details{
	position: relative;
	padding-left: 2.07rem;
}
.max-home .mobile-box-details-title{
	padding: 1.71rem 0 1.21rem;
}
.max-home .mobile-box-details-title>h1{
	width: 16.71rem;
	line-height: 2.57rem;
	font-size: 2.14rem;
	color: #fff;
}
.max-home .mobile-box-details>h4{
	width: 23.14rem;
	line-height: 1.71rem;
	font-size: 1.14rem;
	color: #fff;
}
.max-home .mobile-box-details>p{
	margin: 1.43rem 0 1.07rem;
	line-height: 1.36rem;
	font-size: 1.14rem;
	color: #F8E71C;
	text-decoration: line-through;
}
.max-home .mobile-box-details>button{
	width: 11.43rem;
	height: 3.21rem;
	line-height: 3.21rem;
	border: none;
	text-align: center;
	background-color: #F8E71C;
}
.max-home .mobile-box-details>h5{
	margin: 1.07rem 0 1.57rem;
	line-height: 1.42rem;
	font-size: 1rem;
	color: #fff;
}
.max-home .mobile-box-build>img{
	display: block;
	width: 8.71rem;
	height: 2.85rem;
}
.max-home .mobile-box-build>p{
	margin: 0.42rem 0 0.93rem;
	line-height: 1.42rem;
	font-size: 1.14rem;
	color: #FF8800;
}
.max-home .mobile-box-details>span{
	line-height: 1.71rem;
	font-size: 1rem;
	color: #9B9B9B;
}
.max-home .box-bottom{
	position: relative;
	margin: 1.35rem 0 1.07rem;
	height: 1.71rem;
}



