.max-home .mobile-nav{
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	/*width: 100%;
	height: 100%;*/
	
}
/*.home{
	position: relative;
	width: 100%;
	height: 100%;
}*/
/*menu*/
.max-home .menu{
	position: absolute;
	top: 0;
	left: -73%;
	z-index: 11;
	width: 73%;
	height: 100%;
	color: #FFFFFF;
	background-color: #000000;
	-webkit-transition: all 0.3s;
	-o-transition: all 0.3s;
	transition: all 0.3s; 
}
.max-home .menu-title{	
	width: 100%;
	height: 4.28rem;
	line-height: 4.28rem;
	border-bottom-color: #FFFFFF;
	border-bottom-style: solid;
	border-bottom-width: 2px;
}
.max-home .menu-title>h3{
	padding-left: 2.14rem;
	font-size: 1.14rem;
}
.max-home .menu-shut{
	position: absolute;
	top: 1.5rem;
	right: 1.5rem;
	width: 1.28rem;
	height: 1.28rem;
}
.max-home .menu-shut>i{
	position: absolute;
    display: inline-block;
    width: 2px;
    height: 1.6rem;
    top: -2px;
    background-color: #fff;
    left: 50%;
}
.max-home .menu-shut>i:first-child{
	-webkit-transform: rotate(45deg);
	    -ms-transform: rotate(45deg);
	        transform: rotate(45deg);
}
.max-home .menu-shut>i:last-child{
	-webkit-transform: rotate(-45deg);
	    -ms-transform: rotate(-45deg);
	        transform: rotate(-45deg);
}
.max-home .menu-list{
	padding: 2.86rem 0 0 2.14rem;
	list-style: none;
}
.max-home .menu-list>li{
	margin-bottom: 1.78rem;
	line-height: 1.71rem;
	font-size: 1.43rem;
	color: #fff;
}
.max-home .mune-button{
	position: absolute;
	left: 0;
	bottom: 1.78rem;
	padding-left: 1.43rem;
}
.max-home .mune-button>button{
	position: relative;
	width: 11.43rem;
	height: 3.21rem;
	line-height: 3.21rem;
	border: solid 1px #fff;
	text-align: center;
	color: #fff;
	background-color: transparent;
}
.max-home .mune-button>button:first-child{
	margin-bottom: 0.86rem;
}
.max-home .mobile-language>h3{
	display: inline-block;
	line-height: 1.43rem;
	font-size: 1.14rem;
}
.max-home .mobile-language-arrow{
	display: inline-block;
	margin-left: 0.3rem;
	width: 1.78rem;
	height: 1.78rem;
}
.max-home .mobile-language-arrow>img{
	
}
.max-home .mobile-language-choose{
	display: none;
	position: absolute;
	top: -4rem;
	left: 0.1rem;
	width: 80%;
	list-style: none;
	background-color: black;
}
.max-home .mobile-language-choose>li{
	line-height: 2rem;
	font-size: 1.14rem;
}
.max-home .home-header{
	position: relative;
	height: 4.28rem;
	width: 100%;
}
.max-home .home-header>div{
	
}
.max-home .mobile .home-nav{
	position: absolute;
	top: 0;
	width: 4.28rem;
	height: 100%;
	left: 0;
}
.max-home .nav-button{
	position: absolute;
	top: 1.71rem;
	left: 1.42rem;
}
.max-home .nav-button>i{
	display: block;
	margin-bottom: 0.42rem;
	width: 1.28rem;
	height: 1px;
	background-color: #FFFFFF;
}
.max-home .mobile-nav .home-header>h1{
	padding-top: 0.85rem;
	margin-left: 4.28rem;
	width: 3.42rem;
	height: 2.57rem;
}
.max-home .mobile-nav .home-header>h1>img{
	width: 100%;
	height: 100%;
}
.max-home .mobile-header-right{
	position: absolute;
	height: 100%;
	top: 0;
	right: 1.43rem;
}
.max-home .mobile-nav .home-cart{
	display: inline-block;
	width: 4.28rem;
	height: 100%;
	line-height: 4.28rem;
}
.max-home .mobile-nav .home-cart>img{
	display: block;
	margin: 1.5rem auto;
	width: 1.57rem;
	height: 1.28rem;
}
.max-home .mobile-nav .product-num{
	position: absolute;
	top: 0.93rem;
	left: 2.356rem;
	width: 1.14rem;
	height: 1.14rem;
	line-height: 1.14rem;
	text-align: center;
}
.max-home .mobile-nav .product-num>i{
	display: inline-block;
	width: 1.14rem;
	height: 1.14rem;
	background-color: #FFFFFF;
	border-radius: 50%;
}
.max-home .mobile-nav .product-num>span{
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	font-size: 0.86rem;
	color: #000000;
}
.max-home .mobile-header-right-button{
	vertical-align: top;
	height: 100%;
	display: inline-block;
}
.max-home .mobile-header-right-button>button{
	display: block;
	margin-top: 1.07rem;
	width: 5.7rem;
	height: 2.14rem;
	line-height: 2.14rem;
	border: none;
	color: #000;
	font-size: 0.857rem;
	text-align: center;
	background-color: #F8E71C;
}


