.max-home .mobile-model6{
	position: relative;
	width: 100%;
	height: 100%;
}
.max-home .mobil-emodel6-video{
	padding-top: 4.28rem;
}
.max-home .mobil-model6-video-inner{
	width: 100%;
	height: 16.71rem;
}
.max-home .mobile-model6-details{
	text-align: center;
	margin-top: 2.5rem;
}
.max-home .mobile-model6-details>h2{
	margin: 0 auto;
	width: 19.5rem;
	line-height: 1.71rem;
	font-size: 1.28rem;
	color: #F8E71C;
}
.max-home .mobile-model6-details>h1{
	margin: 1.14rem auto 0;
	width: 23.28rem;
	line-height: 2.57rem;
	font-size: 2.14rem;
	color: #fff;
}
.max-home .mobile-model6-details>p{
	margin: 1.21rem auto 1.14rem;
	line-height: 1.35rem;
	font-size: 1.14rem;
	color: #F8E71C;
	text-decoration: line-through;
}
.max-home .mobile-model6-details>button{
	display: block;
	margin: 0 auto;
	width: 11.42rem;
	height: 3.21rem;
	line-height: 3.21rem;
	text-align: center;
	border: none;
	color: #000;
	background-color: #F8E71C;
}
.max-home .model6>video{
	position: absolute;
	top: 0;
	left: 50%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
	z-index: 0;
}



